.song-container {
    min-height: 60vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    img {
      width: 20%;
      border-radius: 50%;
    }
    h2 {
      padding: 3rem 1rem 1rem 1rem;
    }
    h3 {
      font-size: 1rem;
    }
  }
  
  .song-playing {
    animation: rotation 30s infinite linear;
  }
  
  @media screen and (max-width: 768px) {
    .song-container{
      img{
        width: 60%;
      }
    }
  }